import ApiService from '@/services/ApiService'

export const namespaced = true

export const state = {
  rootUdc: [],
  subUdc: []
}

export const mutations = {
  SET_ROOT_UDC(state, rootUdc) {
    state.rootUdc = rootUdc
  },
  SET_SUB_UDC(state, subUdc) {
    state.subUdc = subUdc
  },
  CLEAR_SUB_UDC(state) {
    state.subUdc = []
  }
}

export const actions = {
  fetchRootUdc({ commit }) {
    return ApiService.fetchRootUdc()
      .then(response => {
        commit('SET_ROOT_UDC', response.data)
      })
      .catch(error => {
        throw error
      })
  },

  fetchSubUdc({ commit }, id) {
    return ApiService.fetchSubUdc(id)
      .then(response => {
        commit('SET_SUB_UDC', response.data)
      })
      .catch(error => {
        throw error
      })
  }
}

export const getters = {
  rootUdc(state) {
    return state.rootUdc
  },
  subUdc(state) {
    return state.subUdc
  }
}
